
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"20px","background":"#fff","width":"1200px","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="24">
					<el-form-item label="合同编号" prop="hetongbianhao">
						<el-input class="list_inp" v-model="form.hetongbianhao" placeholder="请输入合同编号" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="合同名称" prop="hetongmingcheng">
						<el-input class="list_inp" v-model="form.hetongmingcheng" placeholder="合同名称"
							 type="text" 							:readonly="!isAdd||disabledForm.hetongmingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="合同类型" prop="hetongleixing">
						<el-input class="list_inp" v-model="form.hetongleixing" placeholder="合同类型"
							 type="text" 							:readonly="!isAdd||disabledForm.hetongleixing?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="合同文件" prop="hetongwenjian">
						<uploads
							:disabled="!isAdd||disabledForm.hetongwenjian?true:false"
							type="file"
							action="file/upload" 
							tip="请上传合同文件" 
							:limit="1" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.hetongwenjian?form.hetongwenjian:''" 
							@change="hetongwenjianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="签订时间" prop="qiandingshijian">
						<el-date-picker
							class="list_date"
							v-model="form.qiandingshijian"
							format="YYYY 年 MM 月 DD 日"
							value-format="YYYY-MM-DD"
							type="datetime"
							:readonly="!isAdd||disabledForm.qiandingshijian?true:false"
							placeholder="请选择签订时间"
							style="width: 100%;" />
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="终止时间" prop="zhongzhishijian">
						<el-date-picker
							class="list_date"
							v-model="form.zhongzhishijian"
							format="YYYY 年 MM 月 DD 日"
							value-format="YYYY-MM-DD"
							type="datetime"
							:readonly="!isAdd||disabledForm.zhongzhishijian?true:false"
							placeholder="请选择终止时间"
							style="width: 100%;" />
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="用户账号" prop="yonghuzhanghao">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.yonghuzhanghao?true:false"
							v-model="form.yonghuzhanghao" 
							placeholder="请选择用户账号"
							style="width:100%;"
							@change="yonghuzhanghaoChange">
							<el-option v-for="(item,index) in yonghuzhanghaoLists" :label="item" :value="item">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="用户姓名" prop="yonghuxingming">
						<el-input class="list_inp" v-model="form.yonghuxingming" placeholder="用户姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.yonghuxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="确认时间" prop="querenshijian">
						<el-date-picker
							class="list_date"
							v-model="form.querenshijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.querenshijian?true:false"
							placeholder="请选择确认时间" />
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="合同状态" prop="hetongzhuangtai">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.hetongzhuangtai?true:false"
							v-model="form.hetongzhuangtai" 
							placeholder="请选择合同状态"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in hetongzhuangtaiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="房东账号" prop="fangdongzhanghao">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.fangdongzhanghao?true:false"
							v-model="form.fangdongzhanghao" 
							placeholder="请选择房东账号"
							style="width:100%;"
							@change="fangdongzhanghaoChange">
							<el-option v-for="(item,index) in fangdongzhanghaoLists" :label="item" :value="item">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="房东姓名" prop="fangdongxingming">
						<el-input class="list_inp" v-model="form.fangdongxingming" placeholder="房东姓名"
							 type="text" 							:readonly="!isAdd||disabledForm.fangdongxingming?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'hetongxinxi'
	const formName = '合同信息'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		hetongbianhao: getUUID(),
		hetongmingcheng: '',
		hetongleixing: '',
		hetongwenjian: '',
		qiandingshijian: '',
		zhongzhishijian: '',
		yonghuzhanghao: '',
		yonghuxingming: '',
		querenshijian: '',
		hetongzhuangtai: '',
		fangdongzhanghao: '',
		fangdongxingming: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		hetongbianhao : false,
		hetongmingcheng : false,
		hetongleixing : false,
		hetongwenjian : false,
		qiandingshijian : false,
		zhongzhishijian : false,
		yonghuzhanghao : false,
		yonghuxingming : false,
		querenshijian : false,
		hetongzhuangtai : false,
		fangdongzhanghao : false,
		fangdongxingming : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		hetongbianhao: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		hetongmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		hetongleixing: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		hetongwenjian: [
		],
		qiandingshijian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		zhongzhishijian: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		yonghuzhanghao: [
		],
		yonghuxingming: [
		],
		querenshijian: [
		],
		hetongzhuangtai: [
		],
		fangdongzhanghao: [
		],
		fangdongxingming: [
		],
	})
	//合同文件上传回调
	const hetongwenjianUploadSuccess=(e)=>{
		form.value.hetongwenjian = e
	}
	//用户账号列表
	const yonghuzhanghaoLists = ref([])
	//合同状态列表
	const hetongzhuangtaiLists = ref([])
	//房东账号列表
	const fangdongzhanghaoLists = ref([])
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.querenshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='hetongbianhao'){
					form.value.hetongbianhao = row[x];
					disabledForm.value.hetongbianhao = true;
					continue;
				}
				if(x=='hetongmingcheng'){
					form.value.hetongmingcheng = row[x];
					disabledForm.value.hetongmingcheng = true;
					continue;
				}
				if(x=='hetongleixing'){
					form.value.hetongleixing = row[x];
					disabledForm.value.hetongleixing = true;
					continue;
				}
				if(x=='hetongwenjian'){
					form.value.hetongwenjian = row[x];
					disabledForm.value.hetongwenjian = true;
					continue;
				}
				if(x=='qiandingshijian'){
					form.value.qiandingshijian = row[x];
					disabledForm.value.qiandingshijian = true;
					continue;
				}
				if(x=='zhongzhishijian'){
					form.value.zhongzhishijian = row[x];
					disabledForm.value.zhongzhishijian = true;
					continue;
				}
				if(x=='yonghuzhanghao'){
					form.value.yonghuzhanghao = row[x];
					disabledForm.value.yonghuzhanghao = true;
					continue;
				}
				if(x=='yonghuxingming'){
					form.value.yonghuxingming = row[x];
					disabledForm.value.yonghuxingming = true;
					continue;
				}
				if(x=='querenshijian'){
					form.value.querenshijian = row[x];
					disabledForm.value.querenshijian = true;
					continue;
				}
				if(x=='hetongzhuangtai'){
					form.value.hetongzhuangtai = row[x];
					disabledForm.value.hetongzhuangtai = true;
					continue;
				}
				if(x=='fangdongzhanghao'){
					form.value.fangdongzhanghao = row[x];
					disabledForm.value.fangdongzhanghao = true;
					continue;
				}
				if(x=='fangdongxingming'){
					form.value.fangdongxingming = row[x];
					disabledForm.value.fangdongxingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
		context?.$http({
			url: `option/yonghu/yonghuzhanghao`,
			method: 'get'
		}).then(res=>{
			yonghuzhanghaoLists.value = res.data.data
		})
		//由上级字段带出不可改
		disabledForm.value.yonghuxingming = true;
		hetongzhuangtaiLists.value = "生效中,终止".split(',')
		context?.$http({
			url: `option/fangdong/fangdongzhanghao`,
			method: 'get'
		}).then(res=>{
			fangdongzhanghaoLists.value = res.data.data
		})
		//由上级字段带出不可改
		disabledForm.value.fangdongxingming = true;
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//用户账号回调
	const yonghuzhanghaoChange=()=>{
		context?.$http({
			url: `follow/yonghu/yonghuzhanghao?columnValue=` + form.value.yonghuzhanghao,
			method: 'get'
		}).then(res=>{
			//带出用户姓名字段
			if(res.data.data.yonghuxingming){
				form.value.yonghuxingming = res.data.data.yonghuxingming
			}
			//带出房东姓名字段
			if(res.data.data.fangdongxingming){
				form.value.fangdongxingming = res.data.data.fangdongxingming
			}
		})
	}
	//房东账号回调
	const fangdongzhanghaoChange=()=>{
		context?.$http({
			url: `follow/fangdong/fangdongzhanghao?columnValue=` + form.value.fangdongzhanghao,
			method: 'get'
		}).then(res=>{
			//带出用户姓名字段
			if(res.data.data.yonghuxingming){
				form.value.yonghuxingming = res.data.data.yonghuxingming
			}
			//带出房东姓名字段
			if(res.data.data.fangdongxingming){
				form.value.fangdongxingming = res.data.data.fangdongxingming
			}
		})
	}
	//提交
	const save=()=>{
		if(form.value.hetongwenjian!=null) {
			form.value.hetongwenjian = form.value.hetongwenjian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 10px 20px;
		margin: 0px auto;
		background: url(http://clfile.zggen.cn/20240123/cf02fa1d4a2442b581a3d62afcc09015.png) no-repeat left center,#fbfbfb;
		width: calc(100% - 40px);
		border-color: #eee;
		border-width: 1px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #fff;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #fff;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #fff;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 0px solid #27bacc30;
		border-radius: 0px;
		padding: 30px 7%;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
