import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import fangdongList from '@/views/pages/fangdong/list'
import fangdongDetail from '@/views/pages/fangdong/formModel'
import fangdongAdd from '@/views/pages/fangdong/formAdd'
import fangyuanxinxiList from '@/views/pages/fangyuanxinxi/list'
import fangyuanxinxiDetail from '@/views/pages/fangyuanxinxi/formModel'
import fangyuanxinxiAdd from '@/views/pages/fangyuanxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import fangwuzulinList from '@/views/pages/fangwuzulin/list'
import fangwuzulinDetail from '@/views/pages/fangwuzulin/formModel'
import fangwuzulinAdd from '@/views/pages/fangwuzulin/formAdd'
import yuyuexinxiList from '@/views/pages/yuyuexinxi/list'
import yuyuexinxiDetail from '@/views/pages/yuyuexinxi/formModel'
import yuyuexinxiAdd from '@/views/pages/yuyuexinxi/formAdd'
import hetongxinxiList from '@/views/pages/hetongxinxi/list'
import hetongxinxiDetail from '@/views/pages/hetongxinxi/formModel'
import hetongxinxiAdd from '@/views/pages/hetongxinxi/formAdd'
import weixiuxieyiList from '@/views/pages/weixiuxieyi/list'
import weixiuxieyiDetail from '@/views/pages/weixiuxieyi/formModel'
import weixiuxieyiAdd from '@/views/pages/weixiuxieyi/formAdd'
import dianqiweixiuList from '@/views/pages/dianqiweixiu/list'
import dianqiweixiuDetail from '@/views/pages/dianqiweixiu/formModel'
import dianqiweixiuAdd from '@/views/pages/dianqiweixiu/formAdd'
import cuixiutongzhiList from '@/views/pages/cuixiutongzhi/list'
import cuixiutongzhiDetail from '@/views/pages/cuixiutongzhi/formModel'
import cuixiutongzhiAdd from '@/views/pages/cuixiutongzhi/formAdd'
import newsList from '@/views/pages/news/list'
import shenqingtuizuList from '@/views/pages/shenqingtuizu/list'
import shenqingtuizuDetail from '@/views/pages/shenqingtuizu/formModel'
import shenqingtuizuAdd from '@/views/pages/shenqingtuizu/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'fangdongList',
			component: fangdongList
		}, {
			path: 'fangdongDetail',
			component: fangdongDetail
		}, {
			path: 'fangdongAdd',
			component: fangdongAdd
		}
		, {
			path: 'fangyuanxinxiList',
			component: fangyuanxinxiList
		}, {
			path: 'fangyuanxinxiDetail',
			component: fangyuanxinxiDetail
		}, {
			path: 'fangyuanxinxiAdd',
			component: fangyuanxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'fangwuzulinList',
			component: fangwuzulinList
		}, {
			path: 'fangwuzulinDetail',
			component: fangwuzulinDetail
		}, {
			path: 'fangwuzulinAdd',
			component: fangwuzulinAdd
		}
		, {
			path: 'yuyuexinxiList',
			component: yuyuexinxiList
		}, {
			path: 'yuyuexinxiDetail',
			component: yuyuexinxiDetail
		}, {
			path: 'yuyuexinxiAdd',
			component: yuyuexinxiAdd
		}
		, {
			path: 'hetongxinxiList',
			component: hetongxinxiList
		}, {
			path: 'hetongxinxiDetail',
			component: hetongxinxiDetail
		}, {
			path: 'hetongxinxiAdd',
			component: hetongxinxiAdd
		}
		, {
			path: 'weixiuxieyiList',
			component: weixiuxieyiList
		}, {
			path: 'weixiuxieyiDetail',
			component: weixiuxieyiDetail
		}, {
			path: 'weixiuxieyiAdd',
			component: weixiuxieyiAdd
		}
		, {
			path: 'dianqiweixiuList',
			component: dianqiweixiuList
		}, {
			path: 'dianqiweixiuDetail',
			component: dianqiweixiuDetail
		}, {
			path: 'dianqiweixiuAdd',
			component: dianqiweixiuAdd
		}
		, {
			path: 'cuixiutongzhiList',
			component: cuixiutongzhiList
		}, {
			path: 'cuixiutongzhiDetail',
			component: cuixiutongzhiDetail
		}, {
			path: 'cuixiutongzhiAdd',
			component: cuixiutongzhiAdd
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'shenqingtuizuList',
			component: shenqingtuizuList
		}, {
			path: 'shenqingtuizuDetail',
			component: shenqingtuizuDetail
		}, {
			path: 'shenqingtuizuAdd',
			component: shenqingtuizuAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
